.modal {
    transform: translate(-50%, -50%);
    padding: 20px;
    
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    text-align: center;
    opacity: 0.9;
    position: relative;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    backdrop-filter: blur(100px);
}

.success {
    background-color: #5cb85c;
    color: #fff;
}

.error {
    background-color: #c80b05;
    color: #fff;
}